

.container{
    align-items: center;
}


.card-container{
    text-align: center;
    background: linear-gradient(90deg,rgb(255,11,11)-50% , rgba(57,0,0,1) 100%);
    width: 340px;
    height: 620px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    transition: 0.3s;
    animation: ease-in-out;
    border-style: solid;
    border-radius: 15px;
    border-width: 1px;
    border-color: black;
    margin: auto;
    
}


.card-container:hover{
    transform: scale(1,1);
    box-shadow: 0px 0px 10px 0px;
    border-radius: 15px;
}

.components-container{

    margin-top: 10px;
    text-align: center;


}

.card-Image{
    background-color: thistle;
    width: 100%;
    height: 100%;
    -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  -webkit-user-drag: none;
  
  -webkit-touch-callout: none;
}

.image-container {
     
    display: flex;
    overflow: hidden;
    height: 410px;
    width: 290px;
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    border-style: solid;
    border-radius: 15px;
    border-width: 3px;
    border-color: white;



    
}


h3,p{
   margin: 0;
    padding: 0;
    text-decoration: none;
    color: white;
}

.card-collectionNameContainer {
    
    font-family: 'Lato';
    font-weight: 900;
   font-style: italic;

     
    font-size: 20px;
    text-align: center;
    color: white;
}
.card-cardNameContainer {
    
    font-family: 'Lato';
    font-weight: 900;
    font-style: italic;
    text-rendering: optimizeLegibility;
    
    font-size: 20px;
    text-align: center;
    margin-top: 20px;
    color: white;

}

.buyButton button{
    margin-top: 20px;
    font-family: 'Lato';
    font-weight: 900;
    font-style: italic;

    background-color: white;
    border-style: none;
    border-radius: 15px;
    width: 150px;
    height: 45px;
    transition: 0.2s;
    font-size: 20px;
   
}


h3{
    font-weight: bold;


}
a{
    
    color: red;
    text-decoration: none;
    font-weight:bolder;
  
}

