
.pricing__section {
  color: #fff;
  padding: 130px  0px;
  }
.App-Container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.heading {
   display: flex;
    justify-content: center;
    align-items: center;
}

  .jumbotron{
  align-items: center;
 width: 80%;
 margin: auto;
}

.LeftButton{
 display: flex;
 justify-content: center;
 align-items: center;
 width: 160px;
 height: 160px;
 float: left;
}    
.RightButton {
 
 display: flex;
 justify-content: center;
 align-items: center;
 position: center;
 width: 160px;
 height: 160px;
 float: right;
}

.botonIzquierdo{
 
 width: 100%;
 height: 100px;

}
.img_boton_izquierdo{
 
 float: right;
 object-fit: contain;
 width: 60%;
 
}
.img_boton_derecho{
 
 float: left;
 object-fit: contain;
 width: 60%;
 
}
.botonDerecho{
 
 width: 100%;
 height: 100px;
}
text{
   box-sizing: border-box;   
   margin: 0;
   padding: 0;
   font-family: 'Lato', sans-serif;
 }
 
